import { ReactNode } from 'react';
import { Cell, IconContainer, Section } from '@xelene/tgui';

import {Icon28Heart} from "@xelene/tgui/dist/icons/28/heart";

type CellProps = {
  id: number;
  icon: ReactNode;
  text: string;
}

const cells: CellProps[] = [
  {
    id: 1,
    icon: <Icon28Heart />,
    text: 'little',
  },
  {
    id: 2,
    icon: <Icon28Heart />,
    text: 'middle',
  },
  {
    id: 3,
    icon: <Icon28Heart />,
    text: 'max',
  },
];

export const CellSection = () => (
  <Section header="Chose your plan" footer="you can chose one of these plans">
    {cells.map((cell) => (
      <Cell
        key={cell.id}
        before={<IconContainer>{cell.icon}</IconContainer>}
      >
        {cell.text}
      </Cell>
    ))}
  </Section>
);
